@charset "UTF-8";
.text-center {
  text-align: center;
}

/** Media Queries **/
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: #ffffff;
  height: 100vh;
  background: rgb(34, 193, 195);
  background: linear-gradient(180deg, rgb(157, 228, 228) 0%, rgba(45, 203, 253, 0.0088235978) 40%);
}

p {
  color: #000000;
  font-size: 2rem;
  line-height: 2;
}

.container {
  width: 90%;
  margin: 0 auto;
}

.seccion {
  padding: 8rem 0;
}

a {
  text-decoration: none;
  color: #262626;
  font-size: 2rem;
}

img {
  max-width: 100%;
  display: block;
}

h1 {
  font-size: 3rem;
  color: #ffffff;
  margin: 0%;
}

h2 {
  font-size: 3.2rem;
  color: #000000;
  margin: 0%;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

nav {
  width: 100vw;
  display: grid;
  place-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  box-shadow: 1px 1px 5px #262626;
  background-color: #0367A6;
  color: #ffffff;
}

.navegacion {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
.navegacion .logo__container {
  display: flex;
  align-items: center;
  gap: 3rem;
}
.navegacion .logo__container img {
  max-width: 4rem;
}
.navegacion .nav__items {
  display: flex;
  gap: 2rem;
}
.navegacion .nav__items a {
  color: #ffffff;
  font-size: 1.8rem;
  padding: 0 2rem;
}
.navegacion .nav__items a:hover {
  border-bottom: 3px solid #397774;
}
.navegacion .nav__signin-signup {
  display: flex;
  align-items: center;
  gap: 3rem;
}
.navegacion .nav__signin-signup a:nth-child(2) {
  font-weight: 500;
}
.navegacion .nav__signin-signup a:first-child {
  color: #ffffff;
}
.navegacion .nav__signin-signup a:first-child:hover {
  transform: scale(1.2);
  z-index: 100;
}
.navegacion .btn {
  display: none;
}

.menu__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  max-width: 1200px;
  height: 100%;
  margin: 0 auto;
}

.menu__links {
  height: 100%;
  transition: transform 0.5s;
  display: flex;
}

.menu__item {
  list-style: none;
  position: relative;
  height: 100%;
  --clip: polygon(0 0, 100% 0, 100% 0, 0 0);
  --transform: rotate(-90deg);
}

.menu__item:hover {
  --clip: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  --transform: rotate(0);
}

.menu__link {
  color: #fff;
  text-decoration: none;
  padding: 0 30px;
  display: flex;
  height: 100%;
  align-items: center;
}

.menu__link:hover {
  background-color: #5e7094;
}

.menu__arrow {
  transform: var(--transform);
  transition: transform 0.3s;
  display: block;
  margin-left: 3px;
}

/* Menu desplegable funcionamiento ↓↓↓ */
.menu__nesting {
  list-style: none;
  transition: clip-path 0.3s;
  clip-path: var(--clip);
  position: absolute;
  right: 0;
  bottom: 0;
  width: max-content;
  transform: translateY(100%);
  background-color: #000;
}

/* Padding del menu desplegable ↓↓↓ */
.menu__link--inside {
  padding: 30px 100px 30px 20px;
}

/* Hover del menu desplegable ↓↓↓ */
.menu__link--inside:hover {
  background-color: #798499;
}

.menu__hamburguer {
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 15px;
  cursor: pointer;
  display: none;
}

.menu__img {
  display: block;
  width: 36px;
}

@media (max-width: 800px) {
  .menu__hamburguer {
    display: flex;
  }
  .menu__item {
    --clip:0;
    overflow: hidden;
  }
  .menu__item--active {
    --transform: rotate(0);
    --background: #5e7094;
  }
  .menu__item--show {
    background-color: var(--background);
  }
  .menu__links {
    position: fixed;
    max-width: 400px;
    width: 100%;
    top: 70px;
    bottom: 0;
    right: 0;
    background-color: #000;
    overflow-y: auto;
    display: grid;
    grid-auto-rows: max-content;
    transform: translateX(100%);
  }
  .menu__links--show {
    transform: unset;
    width: 100%;
  }
  .menu__link {
    padding: 25px 0;
    padding-left: 30px;
    height: auto;
  }
  .menu__arrow {
    margin-left: auto;
    margin-right: 20px;
  }
  .menu__nesting {
    display: grid;
    position: unset;
    width: 100%;
    transform: translateY(0);
    height: 0;
    transition: height 0.3s;
  }
  .menu__link--inside {
    width: 90%;
    margin-left: auto;
    border-left: 1px solid #798499;
  }
}
.btn-primario {
  background-color: #0367A6;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  text-align: center;
  color: #ffffff;
