  padding: 1.2rem 4rem;
  font-weight: 700;
  text-align: center;
  color: #000000;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border-radius: 1rem;
}
.btn-secundario:hover {
  cursor: pointer;
  background-color: #285552;
}
.btn-secundario:hover {
  color: #ffffff;
}

.btn-dam {
  background-color: #0099ff;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  text-align: center;
  color: #000000;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border-radius: 1rem;
}
.btn-dam:hover {
