  background-color: #007acc;
}

.btn {
  border: none;
  font-size: 4rem;
  background-color: transparent;
}

.banner__bg {
  background-image: url("/src/img/wave.svg");
  background-size: cover;
  background-repeat: no-repeat;
  height: 90%;
}

.banner {
  padding: 8rem 0;
  margin-top: 10rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.banner p {
  padding: 1rem 0;
  width: 80%;
}
.banner .banner-left {
  width: 60%;
}
.banner .banner-right {
  width: 40%;
}
.banner .banner-right img {
  width: 90%;
}

.footer {
  background-color: #0367A6;
  color: #ffffff;
}
.footer .footer-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 15rem;
  margin: 0 auto;
}
.footer .footer-grid {
  display: grid;
  gap: 4rem;
  grid-template-columns: repeat(4, 1fr);
  margin-bottom: 2rem;
  padding: 5rem;
  align-items: center;
}
.footer .footer-grid h3 {
  text-align: left;
  margin-bottom: 3rem;
}
.footer .footer-grid .footer-widget a {
  color: #ffffff;
  margin-bottom: 2rem;
}
.footer .footer-copy {
  text-align: center;
  color: #ffffff;
}